<template>
  <v-app-bar app>
    <v-app-bar-nav-icon @click="app.sidebar.opened = !app.sidebar.opened" />

    <v-spacer />

    <v-menu
      left
      bottom
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          icon
          v-bind="attrs"
          v-on="on"
        >
          <v-icon>mdi-dots-vertical</v-icon>
        </v-btn>
      </template>

      <v-list>
        <v-list-item
          key="logout"
          @click="logout"
        >
          <v-list-item-title>退出</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </v-app-bar>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'AppBar',
  data() {
    return {
      switch1: false
    }
  },
  computed: {
    ...mapState({
      app: state => state.app
    })
  },
  methods: {
    async logout() {
      await this.$store.dispatch('user/logout')
      await this.$router.push(`/login?redirect=${this.$route.fullPath}`)
    }
  }
}
</script>

