<template>
  <v-navigation-drawer
    v-model="drawerVal"
    :mini-variant.sync="!app.sidebar.opened"
    app
  >
    <v-list>
      <v-list-item class="px-2">
        <v-list-item-avatar>
          <v-img :src="user.avatar" />
        </v-list-item-avatar>
      </v-list-item>

      <v-list-item link to="/">
        <v-list-item-content>
          <v-list-item-title class="title">
            {{ user.name }}
          </v-list-item-title>
          <v-list-item-subtitle>{{ user.email }}</v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
    </v-list>

    <v-divider />

    <v-list dense nav>
      <v-list-item v-for="item in permissionRoutesVal" :key="item.path" :to="item.path">
        <v-list-item-icon>
          <v-icon>{{ item.meta.icon }}</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title>{{ item.meta.title }}</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list>

  </v-navigation-drawer>
</template>

<script>
import { mapState } from 'vuex'
import { constantRoutes } from '@/router'

export default {
  name: 'Navigation',
  data() {
    return {
      drawer: true
    }
  },
  computed: {
    ...mapState({
      app: state => state.app,
      user: state => state.user
    }),
    permissionRoutesVal() {
      const result = []
      constantRoutes.forEach(item => {
        if (item.path === '/') {
          item.children.forEach(router => {
            if (!router.hidden) {
              result.push(router)
            }
          })
        }
      })
      return result
    },
    drawerVal: {
      get() {
        const name = this.$vuetify.breakpoint.name
        if (name === 'xs' || name === 'sm' || name === 'md') {
          return this.app.sidebar.opened
        } else {
          return true
        }
      },
      set(val) {
        const name = this.$vuetify.breakpoint.name
        if (name === 'xs' || name === 'sm' || name === 'md') {
          this.app.sidebar.opened = val
        }
      }
    }
  }
}
</script>

